@import "../../scss/abstracts";
.icons-social {
    text-align: center;
    padding-top: 1rem;
    margin-bottom: 5vh;
}

.icon-link {
    padding: 1rem 9rem;
    padding: 1rem 4rem;
    width: 3rem;
    height: 3.5rem;
}

.icon {
    display: inline-block;
    // padding: 1rem 9rem;
    padding: 1rem 5rem;
    // width: 3rem;
    // height: 3.5rem;
    // height: 100%;
    // width: 100%;
    background-color: $color-primary;
    // cursor: pointer;
    // transition: all 0.5s ease;
    // transform: scale(1);
    @include respond(med-screen) {
        // padding: 1rem 5rem;
        // width: 3rem;
        // height: 3rem;
    }
    @include respond(smaller-screen) {
        // padding: 1rem 5rem;
        // width: 3rem;
        // height: 4rem;
    }
    &:hover {
        @include animate(gradient, .7, ease, normal, infinite, none);
    }
    &--linkedin {
        -webkit-mask: url(../../assets/linkedin-in-brands.svg) no-repeat center/contain;
        mask: url(../../assets/linkedin-in-brands.svg) no-repeat center/contain;
        &:hover {
            @include smaller-size;
        }
    }
    &--email {
        -webkit-mask: url(../../assets/envelope-solid.svg) no-repeat center/contain;
        mask: url(../../assets/envelope-solid.svg) no-repeat center/contain;
        &:hover {
            @include smaller-size;
        }
    }
    &--github {
        -webkit-mask: url(../../assets/github-brands.svg) no-repeat center/contain;
        mask: url(../../assets/github-brands.svg) no-repeat center/contain;
        &:hover {
            @include smaller-size;
        }
    }
}